/*============================= FEATURES =====================================*/

.narrow{
	width: 75%;
	margin: 0 auto;
	padding-top: 4rem;
}
#features{

	p{
		margin: 3rem 0 1.5rem;
		line-height: 1.9em;
		letter-spacing: .05rem;
		word-spacing: .2rem;
	 }
}

.btn-sm{
	padding: .7rem 1rem;
	font-size: 1rem;
	text-transform: uppercase;
	margin: 1rem;
	color: $base-white;

}
.btn-gray{
	background: $lighter-gray;
	&:hover,
	&:focus{
		background: darken($lighter-gray, 5%);
		color: $base-white
	}
}
.btn-blue{
	background: $base-blue;
	&:hover,
	&:focus{
		background: darken($base-blue, 5%);
		color: $base-white
	}
}
/* ovveride animate.css default duration */
#features .fadeIn{
	animation-duration: 1.5s;
}

/* Jumbotron Styles */
.jumbotron{
	margin-top: 2rem;
	margin-bottom: 0;
	padding: 1rem 0 3rem;
}

#features .feature{
	text-align: center;
	h4{
		color: darken($lighter-gray, 6%);
		margin-top: 1rem;
		text-transform: uppercase;
		font-weight: 700;
	}
	p{
		margin: 1.5rem 0  3rem 0;
	}
}

.fa-stack.fa-2x{
	font-size: 2.5rem;
}


.fa-stack .fas{
	color: $base-red;
	&.fa-inverse{
		font-size: 2.8rem;
		color: $base-white;
	}
}
#features .landing{
	height: 70vh;
}
#features .home-inner{
	background: url(../../img/builder.jpg) center center/cover;
	&:after{
		background: rgba(0, 0, 0, .7)
	}
}
